import React, { useState } from "react";
import ReactPlayer from "react-player";
import { config } from "../../config/api";
import FormStyleProvider from "./FormStyleProvider";

const Form2 = ({ numbers }) => {
  const [url, seturl] = useState(`${config.apiResource}${numbers?.[0]?.video}`);
  const handleVideo = (url) => {
    console.log("====================================");
    console.log(url);
    console.log("====================================");
    seturl(`${config.apiResource}${url}`);
  };
  return (
    <FormStyleProvider>
      <div className={`step__form--general page--2 d-flex flex-column flex-lg-row `}>
        <div className="step__form--general--left">
          <p className="fs__title--main fw-bolder">Bài báo cáo</p>
          <div className="step__form__video">
            <ReactPlayer
              url={url}
              controls={true}
              muted={true}
              playing={true}
              style={{ width: "100%", height: "100%" }}
            />
          </div>
          {numbers?.map((number) => (
            <p
              onClick={() => handleVideo(number?.video)}
              className="d-flex justify-content-between"
            >
              Chỉ số {number?.name_i} <span>{number?.number_order}</span>
            </p>
          ))}
        </div>
        <div className="step__form--general--right">
          <p className="fs__text">
            Đây là bản báo cáo các số học ứng dụng của bạn, bức tranh cuộc đời của bạn được chia
            thành 2 bản.
          </p>
          <div className="d-flex mt-5">
            <div className="step__form--icon">
              <i className="far fa-file-pdf"></i>
            </div>
            <div>
              <p className="fs__title fw-bolder">Bản 1</p>
              <p className="fs__text">Bài báo cáo PDF</p>
            </div>
          </div>
          <div className="d-flex mt-3">
            <div className="step__form--icon">
              <i className="far fa-file-video"></i>
            </div>
            <div>
              <p className="fs__title fw-bolder">Bản 2</p>
              <p className="fs__text">Bài báo cáo Video</p>
            </div>
          </div>
        </div>
      </div>
    </FormStyleProvider>
  );
};

export default Form2;
